<template>
	<div class="menu" :class="{ '_opened': isOpened }">
		<div class="menu__close">
			<icon icon="times" @click="toggleMenu(false)" />
		</div>
		<div class="menu__logo"></div>
		<div class="menu__items" @click="clickByMenuArea">
			<router-link class="menu__item" to="/notes">~ Заметки ~</router-link>
			<router-link class="menu__item" to="/">~ Теги ~</router-link>
<!--			<router-link class="menu__item" to="/">~ Таймлайн ~</router-link>-->
<!--			<router-link class="menu__item" to="/tutorial">~ Туториал ~</router-link>-->
			<router-link class="menu__item" to="/contacts">~ Контакты ~</router-link>
<!--			<router-link class="menu__item" to="/">~ Выйти ~</router-link>-->
		</div>
		<div class="menu__dot"></div>
	</div>
</template>

<script>
	export default {
		name: 'Loader',

		data() {
			return {
				isOpened: false,
			}
		},

		methods: {
			/**
			 * @param {boolean} show
			 */
			toggleMenu(show) {
				this.isOpened = Boolean(show);
			},
			clickByMenuArea($event) {
				if ($event.target.classList.contains('menu__item')) {
					this.toggleMenu(false);
				}
			}
		},

		created() {
			app.$menu = this;
		},
	}
</script>

<style lang="less" scoped>
	@import "../css/_vars";

	.menu {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: absolute;
		right: -400px;
		top: 0;
		z-index: 1;
		height: 100vh;
		width: 400px;
		background-color: @color-dark;
		transition: right .3s ease-in-out;
		-webkit-box-shadow: inset 0 -2px 0 2px @color-beige;
		-moz-box-shadow: inset 0 -2px 0 2px @color-beige;
		box-shadow: inset 0 -2px 0 2px @color-beige;
		padding: 2px 0;

		&._opened {
			right: 0;
		}

		&__items {
			padding: 50px 0;
			flex: 1;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}

		&__item {
			padding: 10px;
			margin: 5px 0;
			text-align: center;
			font-weight: bold;
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}
		}

		&__logo {
			margin: 100px auto auto;
			width: 64px;
			height: 64px;
			background-image: url("../resources/images/logo_small.png");
			background-repeat: no-repeat;
			background-size: contain;
			background-position: center;
			border-radius: 50%;
		}

		&__dot {
			margin: auto auto 100px;
			width: 10px;
			height: 10px;
			border-radius: 50%;
			background-color: @color-beige;
		}

		&__close {
			position: absolute;
			top: 15px;
			right: 25px;
			color: white;
			font-size: 18px;
			padding: 10px;
			cursor: pointer;
		}
	}
</style>



